<template>
  <div class="zk-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" type="warning" size="small" @click="open()">{{$t('common.add')}}</el-button>
    </div>
    <el-form label-width="70px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="版本号：">
            <el-input v-model.trim="versionForm.versionNo"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" icon="el-icon-search" @click="getTableData" size="small">{{$t('common.search') }}
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table :data="versionInfo.list" stripe>
      <el-table-column prop="name" align="center" label="版本名称" width="150">
      </el-table-column>
      <el-table-column prop="versionNo" align="center" label="版本号" width="150">
      </el-table-column>
      <el-table-column prop="path" align="center" label="保存路径" width="300" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="createTime" align="center" label="创建日期" width="150">
      </el-table-column>
      <!-- <el-table-column
        prop="creator"
        align="center"
        label="创建人"
        width="150">
      </el-table-column> -->
      <el-table-column prop="remark" align="center" label="描述">
      </el-table-column>
      <el-table-column width="180" align="center" :label="$t('common.operation')">
        <template slot-scope="props">
          <el-button type="text" size="mini" icon="el-icon-edit" @click="edit(props.row)">{{$t('common.modify') }}</el-button>
          <el-button type="text" size="mini" icon="el-icon-download" @click="download(props.row)">
            {{$t('common.download')}}
          </el-button>
          <el-button type="text" class="btn-text-red" size="mini" icon="el-icon-delete" @click="deleteVersion(props.row)">
            {{$t('common.delete')}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background @current-change="handleCurrentChange" :current-page.sync="current" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="versionInfo.total">
    </el-pagination>
    <!-- 添加 弹出框 -->
    <el-dialog :title="versionTitle" :visible.sync="addVisible" width="40%" @close="close" center>
      <el-form ref="modalForm" :model="modalForm" :rules="inputRules" status-icon label-width="100px" size="small">
          <el-row :gutter="10">
            <el-col :span="24">
        <el-form-item label="软件包来源" prop="radio">
              <el-radio-group v-model="modalForm.method" @change="radioChange">
                <el-radio label="Local">{{$t('common.local')}}</el-radio>
                <el-radio label="Remote">{{$t('common.server')}}</el-radio>
              </el-radio-group>
        </el-form-item>
            </el-col>
          </el-row>
        <el-row :gutter="10" v-show="modalForm.method === 'Local'">
          <el-col :span="19">
            <el-form-item class="is-required" label="软件包路径" prop="localPath" ref="inputFileLocal">
              <el-input v-model="radio" placeholder="请上传本地软件包"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="4">
            <input type="file" class="input-file" :value="fileValue" @change="handleChange($event)" accept=".zip, .tar, .gz">
            <el-button type="primary" size="mini">{{$t('common.viewFile')}}</el-button>
          </el-col>
        </el-row>
        <el-row :gutter="10" v-show="modalForm.method === 'Remote'">
          <el-col :span="23">
            <el-form-item class="is-required" label="软件包路径" prop="remotePath" ref="inputFileRemote">
              <el-input v-model.trim="modalForm.path" placeholder="请输入路径(例 http://10.86.1.10:8080/zk/zk-1.0.tar"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
          <el-row :gutter="10">
            <el-col :span="23">
        <el-form-item :label="$t('coreApp.tableVersion.versionName')" prop="name">
              <el-input v-model.trim="modalForm.name" type="text" maxlength="50" placeholder="请输入1-50位字符"></el-input>
        </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="23">
        <el-form-item :label="$t('coreApp.tableVersion.versionNum')" prop="versionNo">
              <el-input v-model.trim="modalForm.versionNo" type="text" maxlength="20" placeholder="请输入1-20位字符"></el-input>
        </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="23">
        <el-form-item label="版本描述" prop="remark">
              <el-input v-model.trim="modalForm.remark" type="textarea" maxlength="100" placeholder="请输入1-100位字符"></el-input>
        </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="23">
        <el-form-item>
              <el-progress :text-inside="true" :stroke-width="18" :percentage="percentageNum" status="success"></el-progress>
        </el-form-item>
            </el-col>
          </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveAdd()" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="close" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
    <el-dialog title="修改zk版本" :visible.sync="editVisible" @close="editClose" width="40%" center>
      <el-form ref="editForm" :model="editForm" :rules="inputRules" label-width="100px" size="small">
        <el-form-item :label="$t('coreApp.tableVersion.versionName')" prop="name">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="editForm.name" maxlength="50"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.versionNum')">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="editForm.versionNo" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.type')">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input :value="editForm.fileType === 'FULL' ? '全量' : '增量'" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.createTime')">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="editForm.createTime" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.createPerson')">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="editForm.creator" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.path')">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="editForm.path" disabled></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item :label="$t('coreApp.tableVersion.desc')">
          <el-row :gutter="10">
            <el-col :span="23">
              <el-input v-model.trim="editForm.remark" maxlength="100" type="textarea"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveEdit()" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="editClose" size="small">{{$t('common.cancel')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Api from '../../constants/Api'

export default {
  data() {
    return {
      current: 1,
      pageSize: 10,
      versionForm: {},
      versionTitle: '添加zk版本',
      addVisible: false,
      modalForm: {
        method: 'Local'
      },
      inputRules: {
        path: [
          { required: true, message: '请输入', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入版本名称', trigger: 'blur' }
        ],
        versionNo: [
          { required: true, message: '请输入版本号', trigger: 'blur' }
        ]
      },
      radio: '',
      percentageNum: 0,
      editVisible: false,
      editForm: {},
      fileValue: ''
    }
  },
  computed: {
    ...mapState({
      versionInfo: state => state.zookeeper.versionInfo
    })
  },
  created() {
    this.getTableData()
  },
  methods: {
    ...mapActions('zookeeper', ['getZkVersionList', 'versionOperation', 'zkDownLoad']),
    getTableData() {
      const vm = this
      const params = {
        pageNo: vm.current,
        limit: vm.pageSize,
        baseParam: {
          moType: 'Zookeeper',
          versionNo: vm.versionForm.versionNo
        }
      }
      vm.getZkVersionList(params)
    },
    open() {
      this.modalForm = {
        method: 'Local'
      }
      this.radio = ''
      this.fileValue = '' // 重置input file值
      this.percentageNum = 0
      this.addVisible = true
    },
    close() {
      this.addVisible = false
      this.$refs['modalForm'].clearValidate()
      this.fileValue = '' // 重置input file值
    },
    editClose() {
      this.editVisible = false
      this.$refs['editForm'].clearValidate()
    },
    edit(props) {
      const vm = this
      vm.editForm = { ...props }
      vm.editVisible = true
    },
    saveEdit() {
      const vm = this
      vm.$refs['editForm'].validate(valid => {
        if (valid) {
          vm.versionOperation({
            url: Api.coreVersionUpdate,
            data: vm.editForm,
            callback: () => {
              vm.$message({
                type: 'success',
                message: '修改成功'
              })
              vm.getTableData()
              vm.editClose()
            },
            callbackError: (error) => {
              vm.$message({
                type: 'error',
                message: (error.data && error.data.message) || '修改失败'
              })
            }
          })
        }
      })
    },
    saveAdd() {
      const vm = this
      vm.$refs['modalForm'].validate(valid => {
        if (valid) {
          if (!vm.radio && vm.modalForm.method === 'Local') {
            vm.$message({
              type: 'warning',
              message: '请上传软件包'
            })
            return
          }
          if (!vm.modalForm.path && vm.modalForm.method === 'Remote') {
            vm.$message({
              type: 'warning',
              message: '请输入软件包路径'
            })
            return
          }
          let method = vm.modalForm.method
          const params = vm.modalForm
          params.moType = 'Zookeeper'
          params.fileType = 'FULL'
          params.creator = localStorage.getItem('userName') || 'admin'
          const formData = new FormData()
          Object.keys(params).forEach((value) => {
            if (params[value]) {
              formData.append(value, params[value])
            }
          })
          vm.$axios({
            method: 'post',
            url: Api.coreVersionAdd,
            params: {
              method
            },
            data: formData,
            onUploadProgress: (progressEvent) => {
              if (progressEvent.lengthComputable) {
                vm.percentageNum = Math.round((progressEvent.loaded / progressEvent.total) * 100)
              }
            }
          }).then((res) => {
            if (res.code === '0000') {
              vm.getTableData()
              vm.$message.success('添加成功')
              vm.close()
            }
          }).catch(error => {
            vm.percentageNum = 0
            vm.$message.error((error.data && error.data.message) || '添加失败')
          })
        }
      })
    },
    handleChange(e) {
      let box = ['.zip', '.gz', '.tar']
      let suffixName = this.commonFunc.getSuffixName(e.target.files[0].name)
      console.log(suffixName)
      if (box.includes(suffixName)) {
        this.modalForm.sourceFile = e.target.files[0]
        this.radio = e.target.value
      } else {
        this.$message({
          type: 'warning',
          message: '文件类型不正确,请重新选择文件类型(.tar / .tar.gz / .zip)'
        })
      }
    },
    radioChange(val) {
      this.modalForm.method = val
      this.$nextTick(() => {
        if (this.$refs.inputFileRemote) {
          this.$refs.inputFileRemote.resetField()
        }
        if (this.$refs.inputFileLocal) {
          this.$refs.inputFileLocal.resetField()
        }
        this.fileValue = ''
        this.radio = ''
        this.modalForm.sourceFile = ''
        this.modalForm.path = ''
      })
    },
    deleteVersion(props) {
      console.log(props)
      const vm = this
      vm.$confirm(`是否确认删除版本${props.name}？`, '删除', {
        confirmButtonText: vm.$t('common.ok'),
        cancelButtonText: vm.$t('common.cancel'),
        type: 'warning',
        center: true
      }).then(() => {
        vm.versionOperation({
          url: Api.coreVersionDelete,
          data: { id: props.id },
          callback: () => {
            vm.$message({
              type: 'success',
              message: '删除成功'
            })
            if (vm.versionInfo.list.length === 1) {
              vm.current = vm.current - 1
            }
            vm.getTableData()
          },
          callbackError: (error) => {
            vm.$message({
              type: 'error',
              message: (error.data && error.data.message) || '删除失败'
            })
          }
        })
      }).catch(() => { })
    },
    download(row) {
      const vm = this
      vm.conmmonMethods.makePopBox(vm, '下载').then(() => {
        vm.downPath = row.path
        vm.zkDownLoad(vm)
      })
    },
    handleCurrentChange(val) {
      this.current = val
      this.getTableData()
    }
  }
}
</script>

<style lang="scss" scoped>
.input-file {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
</style>



